import React, { Component } from 'react';
import './App.css';
import { AgGridReact } from 'ag-grid-react';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import 'ag-grid-enterprise';

class ClubOfficers extends Component {
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {
      columnDefs: [{
        headerName: "Club Id", enableRowGroup: true, field: "clubId", sortable: true, hide:true, filter: true, resizable: true
      },{
        headerName: "Division",  enableRowGroup: true, field: "division", sortable: true, sort: 'asc', filter: true, resizable: true
      },{
        headerName: "Area",  enableRowGroup: true, field: "area", sortable: true, sort: 'asc', filter: true, resizable: true
      },{
        headerName: "Club Name",  enableRowGroup: true, field: "clubName", sortable: true, sort: 'asc', filter: true, resizable: true
      },{
        headerName: "Officer Role Selected", enableRowGroup: true, field: "officer", sortable: true, sort: 'asc', filter: true, resizable: true
      },{
        headerName: "Training Date", enableRowGroup: true, field: "eventDate", sortable: true, filter: true, resizable: true
      },{
        headerName: "First Initial", field: "firstName", sortable: true, filter: true, resizable: true,  enableRowGroup: true, 
      },{
        headerName: "Last Initial", field: "lastName", sortable: true, filter: true, resizable: true,  enableRowGroup: true,
      },{
        headerName: "Member Id", field: "memberId", sortable: true, filter: true, resizable: true,  enableRowGroup: true, 
      }],
      statusBar: {
        statusPanels: [
            { statusPanel: 'agTotalAndFilteredRowCountComponent', align: 'left' }
        ]
      }
    }
  }

  componentDidMount() {
    const getData = () => {
      return fetch('https://a5slwb8wx6.execute-api.us-east-1.amazonaws.com/dev/list-officer-attendees', {retries: 4,retryDelay: 1000}).then(resp => resp.json()).then(registrants => registrants.map(r => {
        let rObj = r;
        if (r.eventId === 99851275316){
          rObj["eventDate"] = '2020-12-19';
        }
        if (r.eventId === 99632810126){
          rObj["eventDate"] = '2021-01-08';
        }
        if (r.eventId === 99669431236){
          rObj["eventDate"] = '2021-01-11';
        }
        if (r.eventId === 99072824214){
          rObj["eventDate"] = '2021-01-14';
        }
        return rObj;
      }));
    };

    Promise.all([
      getData()
    ]).then(data => data.reduce(function (answerObjects, response) {
        return Array.prototype.concat(answerObjects, response);
      }, [])
    )
    .then(rowData => this.setState({rowData}))
    .catch(function (error) {
      // if there's an error, log it
      console.log(error);
    })
  }

  render() {
    return (
      <div
        className="content ag-theme-alpine"
        style={{
        height: '100%',
        width: '100%' 
      }
      }
      >
        <AgGridReact
          columnDefs={this.state.columnDefs}
          rowData={this.state.rowData}
          statusBar={this.state.statusBar}
          rowGroupPanelShow='always'>
        </AgGridReact>
      </div>
    );
  }
}

export default ClubOfficers;